export class Department {
    public ID: number;
    public DepartmentName: string;
    public DepartmentDesc: string;
    public DTypes: DType[];
    constructor(ID?,DepartmentName?){
        this.ID =0;
        this.DepartmentName='';
        this.DepartmentDesc='';
        this.DTypes = [];
    }

}

export class DType {
    public ID: number;
    public DepartmentID:number;
    public TypeName: string;
    public TypeDesc: string;
    public SubTypes: SubType[];
    constructor(DepartmentID,ID?,TypeName?){
        this.ID =0;
        this.DepartmentID=DepartmentID;
        this.TypeName='';
        this.TypeDesc='';
        this.SubTypes = [];
    }
        
}


export class SubType {
    public ID: number;
    public TypeID:number;
    public SubTypeName: string;
    public SubTypeDesc: string;
    
    constructor(TypeID, ID?,SubTypeName?){
        this.ID =0;
        this.TypeID=TypeID;
        this.SubTypeName='';
        this.SubTypeDesc='';
    }
        
}