export class PurchaseOrder {
    products: Product[];
    ID: number;
    PurchaseOrderNumber: string;
    POType: string;
    POTypeID: number;
    POTypeName: string;
    PurchaseOrderDescription: string;
    OrderDate: string;
    ShipDate: string;
    CancelDate: string;
    Notes: string;
    VendorID: number;
    VendorName: string;

    BrandID: number;
    BrandName: string;
    BrandCategory: string;

    StoreID: number;
    StoreNumber: string;
    StoreName: string;

    constructor() {
        this.Notes = '';
        this.PurchaseOrderDescription = '';
        this.products = [];
    }
}


