import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";

@Component({
  selector: 'app-purchase-order-edit-delete-btn',
  templateUrl: './purchase-order-edit-delete-btn.component.html',
  styleUrls: ['./purchase-order-edit-delete-btn.component.css']
})
export class PurchaseOrderEditDeleteBtnComponent implements ICellRendererAngularComp {

  constructor() { }
  public params: any;

  agInit(params: any): void {
    this.params = params;
  }
  public exportPO() {
    this.params.context.componentParent.exportPO(this.params.node.data);
  }

  public viewEditPO() {
    this.params.context.componentParent.viewEditPO(this.params.node.data);
  }

  public deletePO() {
    this.params.context.componentParent.deletePO(this.params.node.data);
  }
  refresh(): boolean {
    return false;
  }

}
