import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable ,  of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { Department, DType, SubType } from '../helpers/department';

@Injectable()
export class DepartmentService {
  private departmentUrl;
  public isEdit=false;
  public isCreate=false;
  public isView=false;
  public selectedDepartment: Department; // used for editing store
  public selectedType: DType;
  public selectedSubType: SubType;

  constructor(private httpClient: HttpClient) { 
    this.departmentUrl = 'assets/api/department.php';
    this.selectedDepartment = new Department(); 
  }

  getAllDepartments (): Observable<any> {
    var obj = {'action':'getAllDepartments',dept:{id:0}};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllDepartments', []))
      );
  }
  getDepartment (deptID): Observable<any> {
    var obj = {'action':'getDepartment',deptID:deptID};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getDepartment', []))
      );
  }
  getTypes (deptID): Observable<any> {
    var obj = {'action':'getTypes',deptID:deptID};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getTypes', []))
      );
  }
  getType (typeID): Observable<any> {
    var obj = {'action':'getType',typeID:typeID};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getType', []))
      );
  }
  getSubTypes (typeID): Observable<any> {
    var obj = {'action':'getSubTypes',typeID:typeID};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getSubTypes', []))
      );
  }
  getSubType (subTypeID): Observable<any> {
    var obj = {'action':'getSubType',subTypeID:subTypeID};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getSubType', []))
      );
  }
  addDepartment(dept): Observable<any> {
    var obj = {'action':'addNewDepartment',dept:dept};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('addDepartment', []))
      );
  }
  addNewType(dtype): Observable<any> {
    var obj = {'action':'addNewType',dtype:dtype};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('addType', []))
      );
  }
  addNewSubType(subType): Observable<any> {
    var obj = {'action':'addNewSubType',subType:subType};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('addSubType', []))
      );
  }
  updateDepartment(dept): Observable<any> {
    var obj = {'action':'updateDepartment',dept:dept};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('updateDepartment', []))
      );
  }
  updateType(dtype): Observable<any> {
    var obj = {'action':'updateType',dtype:dtype};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('updateType', []))
      );
  }
  updateSubType(subType): Observable<any> {
    var obj = {'action':'updateSubType',subType:subType};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('updateSubType', []))
      );
  }
  deleteDepartment(dept): Observable<any> {
    var obj = {'action':'deleteDepartment',dept:dept};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('deleteDepartment', []))
      );
  }
  deleteType(dtypeID): Observable<any> {
    var obj = {'action':'deleteType',dtypeID:dtypeID};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('deleteType', []))
      );
  }
  deleteSubType(subTypeID): Observable<any> {
    var obj = {'action':'deleteSubType',subTypeID:subTypeID};
    return this.httpClient.post(this.departmentUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('deleteSubType', []))
      );
  }
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
      // TODO: better job of transforming error for user consumption
      //this.log(`${operation} failed: ${error.message}`);
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

}
