import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";
@Component({
  selector: 'app-available-width-cell-renderer',
  templateUrl: './available-width-cell-renderer.component.html',
  styleUrls: ['./available-width-cell-renderer.component.css']
})
export class AvailableWidthCellRendererComponent implements ICellRendererAngularComp {

  constructor() { }
  public params: any;

  agInit(params: any): void {
    this.params = params;
  }

  public onChangeAvailableWidth() {
    this.params.context.componentParent.onChangeAvailableWidth(this.params.node.data);
  }


  refresh(): boolean {
    return false;
  }
}
