import { Component } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";
declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-stores-edit-delete-btn',
  templateUrl: './stores-edit-delete-btn.component.html',
  styleUrls: ['./stores-edit-delete-btn.component.css']
})
export class StoresEditDeleteBtnComponent implements ICellRendererAngularComp {

  constructor() { }

  public params: any;

  agInit(params: any): void {
    this.params = params;
  }

  public editStore() {
    this.params.context.componentParent.editStore(this.params.node.data);
  }

  public deleteStore() {
    this.params.context.componentParent.deleteStore(this.params.node.data);
  }
  refresh(): boolean {
    return false;
  }
}
