import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

import { GridOptions } from "ag-grid";
import { Observable } from 'rxjs';
import { Department, DType, SubType } from '../../helpers/department';
import { DepartmentService } from '../../services/department.service';
import { DeptEditDeleteBtnComponent } from '../../helper-components/dept-edit-delete-btn/dept-edit-delete-btn.component';
declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-department',
  templateUrl: './department.component.html',
  styleUrls: ['./department.component.css']
})
export class DepartmentComponent implements OnInit {
  public deptGridApi;
  public deptGridColumnApi;
  private deptGridOptions: GridOptions;
  public columnDefs;
  private selectedID;
  selectedDepartment: Department;

  public isActive: boolean;

  constructor(private router: Router, private deptService: DepartmentService) {
    this.isActive = false;
    this.selectedDepartment = new Department();
    this.selectedID = 1;
    // this.selectedStore.StoreName = '';
    this.deptGridOptions = <GridOptions>{
      context: {
        componentParent: this
      },
      enableColResize: true,
      enableSorting: true,
      enableFilter: true,
      pagination: true,
      paginationAutoPageSize: true
    };
    this.columnDefs = [
      {
        headerName: "ID",
        field: "ID",
        minWidth: 100,
        maxWidth: 100
      },
      {
        headerName: "Department Name",
        field: "DepartmentName",
        minWidth: 200,

      },
      {
        headerName: "Description",
        field: "DepartmentDesc",
        minWidth: 200,
      },
      {
        headerName: "Action",
        cellRendererFramework: DeptEditDeleteBtnComponent,
        maxWidth: 200
      }

    ];
  }

  ngOnInit() {
    this.loadDepartments();
  }

  loadDepartments() {
    this.deptService.getAllDepartments().subscribe(
      res => {
        //console.log("response at component is: "+ res);    
        if (res) {
          //console.log('array');
          this.deptGridOptions.rowData = res;
        }
        else {
          //console.log(typeof(res));
          this.deptGridOptions.rowData = [];
        }

        this.isActive = true;
      }
    );
  }

  addNewDepartment() {
    // this.deptService.isEdit = false;
    // this.deptService.isCreate = true;
    // this.deptService.isView=false;
    this.router.navigate(['/actionDepartment', 'create', '0']);
  }

  viewEditDepartment(obj) {
    this.router.navigate(['/actionDepartment', 'viewEdit', obj.ID]);

    // console.log(obj);
    // this.deptService.isEdit = false;
    // this.deptService.isCreate = false;
    // this.deptService.isView=true;
    // this.deptService.selectedDepartment.ID = obj.ID;
    // this.deptService.selectedDepartment.DepartmentName = obj.DepartmentName;
    // this.deptService.selectedDepartment.DepartmentDesc = obj.DepartmentDesc;
    // // var component = this;
    // this.deptService.getTypes(obj.ID).subscribe(
    //   res => {
    //     console.log("response at component is: "+ res);    
    //     if(res){
    //       console.log('array');
    //       this.deptService.selectedDepartment.DTypes = res;
    //       this.router.navigate(['/actionDepartment','viewEdit', obj.ID]);
    //     }    
    //     else {
    //       console.log(typeof(res));
    //       this.deptService.selectedDepartment.DTypes = [];
    //       this.router.navigate(['/actionDepartment','viewEdit',obj.ID]);
    //     }

    //     this.isActive = true;
    //   }
    // );
    // this.deptService.selectedDepartment = obj;

    // //console.log(this.deptService.selectedDepartment);

  }

  deleteDepartment(obj) {
    //console.log(obj);
    $('#deleteConfirmationModel').modal('show');
    this.selectedDepartment = obj;
  }
  confirmDeleteDepartment() {
    this.selectedID = 2;
    $('#deleteConfirmationModel').modal('hide');
    this.deptService.deleteDepartment(this.selectedDepartment).subscribe(
      res => {
        if (res) {

          this.deptService.getAllDepartments().subscribe(
            res => {
              //console.log(res);
              if (res) {
                this.deptGridApi.setRowData(res);
              }
              else {
                this.deptGridApi.setRowData([]);
              }
              // this.storesGridOptions.rowData = res;

            });

        }
      });

  }
  cancelDeleteDepartment() {
    $('#deleteConfirmationModel').modal('hide');
  }

  onGridReady(params) {
    this.deptGridApi = params.api;
    this.deptGridColumnApi = params.columnApi;
  }

  onGridSizeChanged(params) {
    var gridWidth = document.getElementById("deptGrid").offsetWidth;
    var columnsToShow = [];
    var columnsToHide = [];
    var totalColsWidth = 0;
    var allColumns = params.columnApi.getAllColumns();
    for (var i = 0; i < allColumns.length; i++) {
      let column = allColumns[i];
      totalColsWidth += column.getMinWidth();
      if (totalColsWidth > gridWidth) {
        columnsToHide.push(column.colId);
      } else {
        columnsToShow.push(column.colId);
      }
    }
    params.columnApi.setColumnsVisible(columnsToShow, true);
    params.columnApi.setColumnsVisible(columnsToHide, false);
    params.api.sizeColumnsToFit();
  }
}
