import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable ,  of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import {Store} from '../helpers/store';


@Injectable()
export class StoresService {
  private storesUrl;
  public resultData;
  public selectedStore: Store; // used for editing store

  constructor(private httpClient: HttpClient) { 
    this.storesUrl = 'assets/api/stores.php';
  }

  getAllStores (): Observable<any> {
    var obj = {'action':'getAllStores',store:{id:0}};
    return this.httpClient.post(this.storesUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllStores', []))
      );
  }
  getStore(storeID): Observable<any> {
    var obj = {'action':'getStore',storeID:storeID};
    return this.httpClient.post(this.storesUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getStore', []))
      );
  }
  addStore (store): Observable<any> {
    var obj = {'action':'addNewStore',store:store};
    return this.httpClient.post(this.storesUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('addStore', []))
      );
  }
  updateStore (store): Observable<any> {
    var obj = {'action':'updateStore',store:store};
    return this.httpClient.post(this.storesUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('udateStore', []))
      );
  }
  deleteStore (store): Observable<any> {
    var obj = {'action':'deleteStore',store:store};
    return this.httpClient.post(this.storesUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('deleteStore', []))
      );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
      // TODO: better job of transforming error for user consumption
      //this.log(`${operation} failed: ${error.message}`);
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }


}
