import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
declare var jquery: any;
declare var $: any;
import { Observable } from 'rxjs';
import { GridOptions } from "ag-grid";
import { BrandEditDeleteBtnComponent } from '../../helper-components/brand-edit-delete-btn/brand-edit-delete-btn.component';
import { Brand, Vendor } from '../../helpers/brand';
// import {SwRange} from '../../helpers/sw-range';
import { BrandService } from '../../services/brand.service';

@Component({
  selector: 'app-brand',
  templateUrl: './brand.component.html',
  styleUrls: ['./brand.component.css']
})
export class BrandComponent implements OnInit {
  public brandGridApi;
  public brandGridColumnApi;
  private brandGridOptions: GridOptions;
  public columnDefs;

  public selectedBrand: Brand;
  public isActive: boolean;
  // public swRange: SwRange[];
  // public vendors: Vendor[];

  constructor(private router: Router, private brandService: BrandService) {
    this.isActive = false;
    this.selectedBrand = new Brand();
    // this.swRange = new SwRange();

    this.brandGridOptions = <GridOptions>{
      context: {
        componentParent: this
      },
      enableColResize: true,
      enableSorting: true,
      enableFilter: true,
      pagination: true,
      paginationAutoPageSize: true
    };
    this.columnDefs = [
      {
        headerName: "ID",
        field: "ID",
        minWidth: 100,
        maxWidth: 100
      },
      {
        headerName: "Brand Name",
        field: "BrandName",
        minWidth: 150,

      },
      {
        headerName: "Vendor Name",
        field: "VendorName",
        minWidth: 150,
      },
      // {
      //   headerName: "Size-Width Name",
      //   field: "SizeWidthName",
      //   minWidth: 150,
      // },
      {
        headerName: "Action",
        cellRendererFramework: BrandEditDeleteBtnComponent,
        maxWidth: 250
      }

    ];
  }

  ngOnInit() {
    this.loadBrands();
  }

  loadBrands() {
    this.brandService.getAllBrands().subscribe(
      res => {
        //console.log("response at component is: "+ res);    
        if (res) {
          //console.log('array returned');
          this.brandGridOptions.rowData = res;
        }
        else {
          //console.log(typeof(res));
          this.brandGridOptions.rowData = []
        }
        // this.swRangeGridOptions.rowData = res;
        this.isActive = true;
      }
    );
  }

  addNewBrand() {
    // this.brandService.isEditBrand = false;
    // this.brandService.isCreateBrand = true;
    // this.brandService.isViewBrand=false;
    this.router.navigate(['/actionBrand', 'create']);
  }
  viewBrand(obj) {
    // console.log(obj);
    // this.brandService.selectedBrand = obj;
    // this.brandService.isEditBrand = false;
    // this.brandService.isCreateBrand = false;
    // this.brandService.isViewBrand=true;
    this.router.navigate(['/actionBrand', 'view', obj.ID])
  }
  editBrand(obj) {
    // console.log(obj);
    // this.brandService.selectedBrand = obj;
    // this.brandService.isEditBrand = true;
    // this.brandService.isCreateBrand= false;
    // this.brandService.isViewBrand=false;
    this.router.navigate(['/actionBrand', 'edit', obj.ID])

  }

  deleteBrand(obj) {
    //console.log("here-2");
    //console.log(obj);
    this.selectedBrand = obj;
    $('#deleteConfirmationModel').modal('show');

  }
  confirmDeleteBrand() {
    // this.selectedID = 2;
    $('#deleteConfirmationModel').modal('hide');
    this.brandService.deleteBrand(this.selectedBrand).subscribe(
      res => {
        if (res) {

          this.brandService.getAllBrands().subscribe(
            res => {
              //console.log(res);
              if (res) {
                this.brandGridApi.setRowData(res);
              }
              else {
                this.brandGridApi.setRowData([]);
              }
              // this.storesGridOptions.rowData = res;

            });

        }
      });

  }

  cancelDeleteBrand() {
    $('#deleteConfirmationModel').modal('hide');

  }

  onGridReady(params) {
    this.brandGridApi = params.api;
    this.brandGridColumnApi = params.columnApi;
  }

  onGridSizeChanged(params) {
    var gridWidth = document.getElementById("brandGrid").offsetWidth;
    var columnsToShow = [];
    var columnsToHide = [];
    var totalColsWidth = 0;
    var allColumns = params.columnApi.getAllColumns();
    for (var i = 0; i < allColumns.length; i++) {
      let column = allColumns[i];
      totalColsWidth += column.getMinWidth();
      if (totalColsWidth > gridWidth) {
        columnsToHide.push(column.colId);
      } else {
        columnsToShow.push(column.colId);
      }
    }
    params.columnApi.setColumnsVisible(columnsToShow, true);
    params.columnApi.setColumnsVisible(columnsToHide, false);
    params.api.sizeColumnsToFit();
  }

}
