import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable ,  of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import {Brand, Vendor} from '../helpers/brand';


@Injectable()
export class BrandService {
  private brandUrl;
  public resultData;
  public selectedBrand: Brand; // used for editing Brand
  public isEditBrand=false;
  public isCreateBrand=false;
  public isViewBrand=false;
  
  constructor(private httpClient: HttpClient) {
    this.brandUrl = 'assets/api/brand.php';
    this.selectedBrand = new Brand();
  }

  getAllBrands (): Observable<any> {
    var obj = {'action':'getAllBrands',brand:{id:0}};
    return this.httpClient.post(this.brandUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllBrands', []))
      );
  }
  getBrand (brandID): Observable<any> {
    var obj = {'action':'getBrand',brandID:brandID};
    return this.httpClient.post(this.brandUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getBrand', []))
      );
  }
  getAllBrandsForVID(vid): Observable<any> {
    var obj = {'action':'getAllBrandsForVID',vid:vid};
    return this.httpClient.post(this.brandUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllBrandsForVID', []))
      );
  }
  getAllVendors():Observable<any> {
    var obj = {'action':'getAllVendors',vendor:{id:0}};
    return this.httpClient.post(this.brandUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllBrands', []))
      );
  }
  
  addBrand (brand): Observable<any> {
    var obj = {'action':'addNewBrand',brand:brand};
    return this.httpClient.post(this.brandUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('addBrand', []))
      );
  }
  updateBrand (brand): Observable<any> {
    var obj = {'action':'updateBrand',brand:brand};
    return this.httpClient.post(this.brandUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('udateBrand', []))
      );
  }
  deleteBrand (brand): Observable<any> {
    var obj = {'action':'deleteBrand',brand:brand};
    return this.httpClient.post(this.brandUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('deleteBrand', []))
      );
  }
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
      // TODO: better job of transforming error for user consumption
      //this.log(`${operation} failed: ${error.message}`);
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
