export class Brand {
    public ID: number;
    public VendorID: number;
    public VendorName: string;
    public BrandName: string;
    public VendorDescription: string;
    public VendorAddress: string;
    public BrandDescription: string;
    public SizeWidthID: number;
    public SizeWidth:any;
    public SizeWidthAvailable:any;
    public SizeWidthName: string;
    public RangeType:string;
    constructor(){
        this.ID =0;
        this.VendorID=0;
        this.VendorName='';
        this.BrandName='';
        this.VendorDescription='';
        this.VendorAddress='';
        this.BrandDescription='';
        this.SizeWidthAvailable = [];
        this.SizeWidthID=0;
        this.SizeWidthName='';
        this.SizeWidth=[];
        this.RangeType='';

    }
}

export class Vendor {

    public ID: number;
    public VendorName: string;
    public VendorDescription: string;
    public VendorAddress: string;
    constructor(ID?){
        this.ID =0;
        this.VendorName='';
        this.VendorDescription='';
        this.VendorAddress='';
    }
}