import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";

@Component({
  selector: 'app-type-edit-delete-btn',
  templateUrl: './type-edit-delete-btn.component.html',
  styleUrls: ['./type-edit-delete-btn.component.css']
})
export class TypeEditDeleteBtnComponent implements ICellRendererAngularComp {

  constructor() { }
  public params: any;

  agInit(params: any): void {
    this.params = params;
  }

  public viewEditType() {
    this.params.context.componentParent.viewEditDType(this.params.node.data);
  }

  public deleteType() {
    this.params.context.componentParent.deleteDType(this.params.node.data);
  }
  refresh(): boolean {
    return false;
  }

}
