import { Component, OnInit } from '@angular/core';
import {Store} from '../../helpers/store';
import { Router, ActivatedRoute} from '@angular/router';
import {StoresService} from '../../services/stores.service';

@Component({
  selector: 'app-edit-store',
  templateUrl: './edit-store.component.html',
  styleUrls: ['./edit-store.component.css']
})
export class EditStoreComponent implements OnInit {
  store = new Store();
  currentStore = new Store()
  // public dataReady = false;
  constructor(private router:Router, private route: ActivatedRoute, private storeService: StoresService) { 
    this.submitted = false; 
  }
  
  submitted = false;
  
  onSubmit() { 
    //console.log(this.storeService.selectedStore);
    this.storeService.updateStore(this.store).subscribe(
      res => {
        this.submitted = true; 
    });
    
  }
  ngOnInit() {
    let id = this.route.snapshot.params['id'];
    //console.log("id: "+ id);
    this.storeService.getStore(id).subscribe(
      res => {
        //console.log("response at component is: "+ res);    
        if(res){
          //console.log('array');

          this.storeService.selectedStore = res[0];
          this.store = this.storeService.selectedStore;
          // console.log(this.store);
          this.currentStore = this.storeService.selectedStore;
          // this.dataReady=true;
          this.submitted = false; 
        }    
        else {
          //console.log(typeof(res));
          
        }
        
        // this.isActive = true;
      }
    );
    
  }
  close(){
    this.router.navigate(['/stores']);
  }

}
