import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { PurchaseOrderService } from '../../services/purchase-order.service';
import { PurchaseOrder } from '../../helpers/purchase-order';
import { IOService } from '../../services/IO.service';
import { FormBuilder, FormGroup } from '@angular/forms';
import { saveAs } from 'file-saver';

const MIME_TYPES = {
  pdf: "application/pdf",
  xls: "application/vnd.ms-excel",
  xlsx: "application/vnc.openxmlformats-officedocument.spreadsheetml.sheet"
}
@Component({
  selector: 'app-export-import-actions',
  templateUrl: './export-import-actions.component.html',
  styleUrls: ['./export-import-actions.component.css']
})
export class ExportImportActionsComponent implements OnInit {
  title: string;
  po: PurchaseOrder;
  public oDate: string;
  public sDate: string;
  public cDate: string;
  public showUploadOptions1: boolean;
  public showUploadOptions2: boolean;
  public showDownloadOptions1: boolean;
  public showDownloadOptions2: boolean;
  fileToUpload: File = null;
  form: FormGroup;

  constructor(private formBuilder: FormBuilder, private router: Router, private route: ActivatedRoute, private poService: PurchaseOrderService, private ioService: IOService) {
    let id = this.route.snapshot.params['id'];
    this.showUploadOptions1 = false;
    this.showUploadOptions2 = false;
    this.showDownloadOptions1 = false;
    this.showDownloadOptions2 = false;
    this.po = new PurchaseOrder();
    this.poService.getPurchaseOrder(id).subscribe(
      res => {
        if (res) {
          console.log(res);
          this.po = res[0];
          console.log(this.po);
          // var x = "12/31/2017";
          var dv = this.po.OrderDate.match(/^(\d{1,2})\/(\d{1,2})\/(\d{4})$/);
          this.po.OrderDate = dv[3] + "-" + dv[1] + "-" + dv[2];

          // this.poService.selectedPO.OrderDate = obj.OrderDate;
          dv = this.po.ShipDate.match(/^(\d{1,2})\/(\d{1,2})\/(\d{4})$/);
          this.po.ShipDate = dv[3] + "-" + dv[1] + "-" + dv[2];

          dv = this.po.CancelDate.match(/^(\d{1,2})\/(\d{1,2})\/(\d{4})$/);
          this.po.CancelDate = dv[3] + "-" + dv[1] + "-" + dv[2];

          this.po.Notes = this.po.Notes;
          // var component = this;

          this.oDate = this.po.OrderDate;
          this.sDate = this.po.ShipDate;
          this.cDate = this.po.CancelDate;
          // this.productGridOptions.rowData = this.po.products;
          this.title = "Export / Import Options " + 'Purchase Order Details - #: ' + this.po.PurchaseOrderNumber;

        }
        else {
          console.log("NO PO Found Error");
        }
      }
    );
  }

  ngOnInit() {
    this.form = this.formBuilder.group({
      inputFile: ['']
    });
  }
  exportPO() {
    window.open("assets/api/exportPO.php/?poID=" + this.po.ID);
  }

  generate3DCartImportTemplate1() {
    window.open("assets/api/exportPOTypeTwo.php/?poID=" + this.po.ID);
  }
  ShowUploadOptionsFor3DExport1() {
    this.showUploadOptions1 = true;
  }

  handleFileInputTemplate1(event) {
    if (event.target.files.length > 0) {
      console.log(event.target.files[0]);
      const file = event.target.files[0];
      this.form.get('inputFile').setValue(file);
      var x = this.form.get('inputFile').value;
      console.log(x.name);
    }
  }
  upload3DCartExport1() {

    const formData = new FormData();
    var x = this.form.get('inputFile').value;
    var filename = "Product_Import_Template_2_" + x.name;
    const EXT = filename.substr(filename.lastIndexOf(".") + 1);

    formData.append('inputFile', this.form.get('inputFile').value);
    formData.append('poID', this.po.ID.toString());
    formData.append('action', "ImportTDCartExportOne");
    this.ioService.uploadTDCartExportOne(formData).subscribe(data => {
      // do something, if upload success
      if (data.type === "application/json") {
        // if (data == null || data === undefined || data == "") {
        console.log("Empty Data ----- " + data);
      }
      else {
        saveAs(new Blob([data], { type: MIME_TYPES[EXT] }), filename);
      }
    }, error => {
      console.log(error);
    });
  }
  download3DCartImportTemplate2() {

  }
  cancelUpload1() {
    this.showDownloadOptions1 = false;
    this.showUploadOptions1 = false;
  }
  ShowUploadOptionsFor3DExport2() {
    this.showUploadOptions2 = true;
  }
  handleFileInputTemplate2(event) {
    if (event.target.files.length > 0) {
      const file = event.target.files[0];
      this.form.get('inputFile').setValue(file);
    }
  }
  upload3DCartExport2() {
    const formData = new FormData();
    formData.append('inputFile', this.form.get('inputFile').value);
    formData.append('poID', this.po.ID.toString());
    formData.append('action', "ImportTDCartExportOne");
    this.ioService.uploadTDCartExportOne(formData).subscribe(data => {
      // do something, if upload success
      console.log("Response Data ---" + data);
      this.showDownloadOptions2 = true;
    }, error => {
      console.log(error);
    });
  }
  download3DCartImportTemplate3() {

  }
  cancelUpload2() {
    this.showDownloadOptions2 = false;
    this.showUploadOptions2 = false;
  }

  closeExportOptions() {
    this.router.navigate(['/purchaseOrders/']);
  }

}
