import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";

@Component({
  selector: 'app-dept-edit-delete-btn',
  templateUrl: './dept-edit-delete-btn.component.html',
  styleUrls: ['./dept-edit-delete-btn.component.css']
})
export class DeptEditDeleteBtnComponent implements ICellRendererAngularComp {

  constructor() { }
  public params: any;

  agInit(params: any): void {
    this.params = params;
  }

  public viewEditDept() {
    this.params.context.componentParent.viewEditDepartment(this.params.node.data);
  }

  public deleteDept() {
    this.params.context.componentParent.deleteDepartment(this.params.node.data);
  }
  refresh(): boolean {
    return false;
  }

}
