import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DepartmentService } from '../../services/department.service';
import { Department, DType, SubType } from '../../helpers/department';
import { GridOptions } from "ag-grid";
import { TypeEditDeleteBtnComponent } from '../../helper-components/type-edit-delete-btn/type-edit-delete-btn.component';
import { SubTypeEditDeleteBtnComponent } from '../../helper-components/sub-type-edit-delete-btn/sub-type-edit-delete-btn.component';


declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-action-department',
  templateUrl: './action-department.component.html',
  styleUrls: ['./action-department.component.css']
})
export class ActionDepartmentComponent implements OnInit {
  dept: Department;
  dtype: DType;
  subType: SubType;

  deptSubmitted: boolean;
  editDeptDetails: boolean;
  editMode: boolean;
  createMode: boolean;

  typeSubmitted: boolean;
  editTypeDetails: boolean;
  editTypeMode: boolean;
  createTypeMode: boolean;

  subTypeSubmitted: boolean;
  editSubTypeDetails: boolean;
  editSubTypeMode: boolean;
  createSubTypeMode: boolean;

  public typeGridApi;
  public typeGridColumnApi;
  private typeGridOptions: GridOptions;
  public typeColumnDefs;

  public subTypeGridApi;
  public subTypeGridColumnApi;
  private subTypeGridOptions: GridOptions;
  public subTypeColumnDefs;

  public isActive: boolean;

  public subTypeSection: boolean;
  public typeSection: boolean;
  public departmentSection: boolean;

  public deptAlertMsg = 'Message';
  public showDeptAlertMsg = false;

  public typeAlertMsg = 'Message';
  public showTypeAlertMsg = false;

  public subTypeAlertMsg = "Message";
  public showSubTypeAlertMsg = false;

  public showViewOnlySubTypeMode = false;
  public selectedTypeID = 0;
  public selectedSubTypeID = 0;
  public selectedTypeName = '';
  public selectedSubTypeName = '';

  // submitted = false;
  constructor(private router: Router, private route: ActivatedRoute, public deptService: DepartmentService) {

    this.typeSubmitted = false;
    this.editTypeDetails = false;
    this.editTypeMode = false;
    this.createTypeMode = false;

    this.subTypeSubmitted = false;
    this.editSubTypeDetails = false;
    this.editSubTypeMode = false;
    this.createSubTypeMode = false;

    this.departmentSection = true;
    this.typeSection = false;
    this.subTypeSection = false;

    this.isActive = false;

    // this.selectedStore.StoreName = '';
    /* Type Grid Options and Coldefs */
    this.typeGridOptions = <GridOptions>{
      context: {
        componentParent: this
      },
      enableColResize: true,
      enableSorting: true,
      enableFilter: true,
      pagination: true,
      paginationAutoPageSize: true
    };
    this.typeColumnDefs = [
      {
        headerName: "ID",
        field: "ID",
        minWidth: 100,
        maxWidth: 100
      },
      {
        headerName: "Type Name",
        field: "TypeName",
        minWidth: 200,

      },
      {
        headerName: "Type Description",
        field: "TypeDesc",
        minWidth: 200,
      },
      {
        headerName: "Action",
        cellRendererFramework: TypeEditDeleteBtnComponent,
        maxWidth: 200
      }

    ];

    /* Type Grid Options and Coldefs */
    this.subTypeGridOptions = <GridOptions>{
      context: {
        componentParent: this
      },
      enableColResize: true,
      enableSorting: true,
      enableFilter: true,
      pagination: true,
      paginationAutoPageSize: true
    };
    this.subTypeColumnDefs = [
      {
        headerName: "ID",
        field: "ID",
        minWidth: 100,
        maxWidth: 100
      },
      {
        headerName: "Sub Type Name",
        field: "SubTypeName",
        minWidth: 200,

      },
      {
        headerName: "Sub Type Description",
        field: "SubTypeDesc",
        minWidth: 200,
      },
      {
        headerName: "Action",
        cellRendererFramework: SubTypeEditDeleteBtnComponent,
        maxWidth: 200
      }

    ];


  }
  ngOnInit() {
    if (this.deptService.isCreate == false && this.deptService.isEdit == false
      && this.deptService.isView == false) {
      this.deptService.isCreate = true;
    }
    // console.log("init");
    let action = this.route.snapshot.params['action'];
    let id = this.route.snapshot.params['id'];
    // console.log(action);

    if (action == 'viewEdit') {

      this.deptService.getDepartment(id).subscribe(
        res => {
          var obj;
          // console.log("response at component is: "+ res);    
          if (res) {
            // console.log(res[0]);
            obj = res[0];
            this.deptService.selectedDepartment.ID = obj.ID;
            this.deptService.selectedDepartment.DepartmentName = obj.DepartmentName;
            this.deptService.selectedDepartment.DepartmentDesc = obj.DepartmentDesc;


          }
          else {
            // console.log(typeof(res));

          }
          this.deptService.isEdit = false;
          this.deptService.isCreate = false;
          this.deptService.isView = true;
          this.deptSubmitted = true;
          this.editDeptDetails = false;
          this.editMode = false;
          this.createMode = false;


          // var component = this;
          this.deptService.getTypes(id).subscribe(
            res => {
              // console.log("response at component is: "+ res);    
              if (res) {
                // console.log('array');
                this.deptService.selectedDepartment.DTypes = res;

              }
              else {
                // console.log(typeof(res));
                this.deptService.selectedDepartment.DTypes = [];

              }
              this.dept = this.deptService.selectedDepartment;
              this.typeGridOptions.rowData = this.dept.DTypes;
            }
          );
        }
      );

    }
    else if (action == 'create') {
      this.deptService.isEdit = false;
      this.deptService.isCreate = true;
      this.deptService.isView = false;

      this.deptSubmitted = false;
      this.editDeptDetails = false;
      this.editMode = false;
      this.createMode = true;
      this.dept = new Department();
    }
    if (this.deptService.isEdit) {
      this.deptSubmitted = false;
      this.editDeptDetails = true;
      this.editMode = true;
      this.createMode = false;
      this.dept = this.deptService.selectedDepartment;
    }
    else if (this.deptService.isCreate) {
      // this.deptSubmitted=false;
      // this.editDeptDetails=false;
      // this.editMode=false;
      // this.createMode=true;
      // this.dept = new Department();
    }
    else if (this.deptService.isView) {
      // this.deptSubmitted=true;
      // this.editDeptDetails=false;
      // this.editMode=false;
      // this.createMode=false;
      // this.dept = this.deptService.selectedDepartment;
    }

    this.subTypeGridOptions.rowData = [];
  }


  onSubmit() {
    if (this.editDeptDetails) {
      this.deptService.updateDepartment(this.dept).subscribe(
        res => {
          this.deptSubmitted = true;
          this.editDeptDetails = false;
          this.editMode = false;
          this.deptAlertMsg = "Department Updated - [ To add 'Type' to this department click 'Add Type' ]";
          this.showDeptAlertMsg = true;

        });
    }
    else {
      this.deptService.addDepartment(this.dept).subscribe(
        res => {
          // console.log(res);
          this.dept.ID = res.ID;
          this.dept.DepartmentName = res.DepartmentName;
          this.dept.DepartmentDesc = res.DepartmentDesc;

          this.deptAlertMsg = "Department Saved - [ To add 'Type' to this department click 'Add Type' ]";
          this.showDeptAlertMsg = true;

          this.deptSubmitted = true;
          this.editDeptDetails = false;
          this.editMode = false;
          this.createMode = false;

        });
    }
  }
  onSubmitType() {
    if (this.editTypeDetails) {
      this.deptService.updateType(this.dtype).subscribe(
        res => {
          this.typeSubmitted = true;
          this.editTypeDetails = false;
          this.editTypeMode = false;
          this.typeAlertMsg = "Type Updated - [ To add 'Sub-Type' to this Type click 'Add Sub-Type' ]";
          this.showTypeAlertMsg = true;
          // updates types grid
          this.loadTypesGrid();
        });
    }
    else {
      this.deptService.addNewType(this.dtype).subscribe(
        res => {
          // console.log(res);
          // this.dtype = res;
          this.dtype = new DType(this.dept.ID);

          this.dtype.ID = res.ID;
          this.dtype.DepartmentID = res.DepartmentID;
          this.dtype.TypeName = res.TypeName;
          this.dtype.TypeDesc = res.TypeDesc;

          this.loadTypesGrid();
          this.dept.DTypes.push(res);

          // console.log(this.dept.DTypes);
          this.typeGridOptions.rowData = this.dept.DTypes;

          this.subTypeGridOptions.rowData = this.dtype.SubTypes;

          this.typeAlertMsg = "Type Saved - [ To add 'Sub-Type' to this Type click 'Add Sub-Type' ]"
          this.showTypeAlertMsg = true;
          this.typeSubmitted = true;
          this.editTypeDetails = false;
          this.editTypeMode = false;
          this.createTypeMode = false;

        });
    }
  }
  loadTypesGrid() {
    this.deptService.getTypes(this.dept.ID).subscribe(
      res => {
        // console.log("response at component is: "+ res);    
        if (res) {
          // console.log('array');
          this.deptService.selectedDepartment.DTypes = res;
          this.dept.DTypes = this.deptService.selectedDepartment.DTypes;
          this.typeGridOptions.rowData = this.dept.DTypes;
        }
        else {
          // console.log(typeof(res));
          this.deptService.selectedDepartment.DTypes = [];
          this.dept.DTypes = this.deptService.selectedDepartment.DTypes;
          this.typeGridOptions.rowData = this.dept.DTypes;
        }
      }
    );
  }
  onSubmitSubType() {
    if (this.editSubTypeDetails) {
      this.deptService.updateSubType(this.subType).subscribe(
        res => {
          this.subTypeSubmitted = true;
          this.editSubTypeDetails = false;
          this.editSubTypeMode = false;
          this.subTypeAlertMsg = "Sub Type Updated";
          this.showSubTypeAlertMsg = true;
          //update subtypes grid
        });
    }
    else {
      this.deptService.addNewSubType(this.subType).subscribe(
        res => {
          // console.log(res);
          this.subType = res;
          this.dtype.SubTypes.push(res);
          // console.log(this.dept.DTypes);
          this.subTypeGridOptions.rowData = this.dtype.SubTypes;
          this.subTypeAlertMsg = "Sub Type Saved"
          this.showSubTypeAlertMsg = true;
          this.subTypeSubmitted = true;
          this.editSubTypeDetails = false;
          this.editSubTypeMode = false;
          this.createSubTypeMode = false;
          this.showViewOnlySubTypeMode = true;
        });
    }
  }
  editDepartmentDetails() {
    this.deptSubmitted = false;
    this.editDeptDetails = true;
    this.editMode = true;
  }
  editDTypeDetails() {
    this.typeSubmitted = false;
    this.editTypeDetails = true;
    this.editTypeMode = true;
  }
  editDSubTypeDetails() {
    this.subTypeSubmitted = false;
    this.editSubTypeDetails = true;
    this.editSubTypeMode = true;
  }
  cancelEditDepartmentDetails() {
    this.deptSubmitted = true;
    this.editDeptDetails = false;
    this.editMode = false;
    this.showDeptAlertMsg = false;

    this.deptService.getDepartment(this.dept.ID).subscribe(
      res => {
        var obj;
        // console.log("response at component is: "+ res);    
        if (res) {
          // console.log(res[0]);
          obj = res[0];
          this.deptService.selectedDepartment.ID = obj.ID;
          this.deptService.selectedDepartment.DepartmentName = obj.DepartmentName;
          this.deptService.selectedDepartment.DepartmentDesc = obj.DepartmentDesc;


        }
        else {
          // console.log(typeof(res));

        }
        this.deptService.isEdit = false;
        this.deptService.isCreate = false;
        this.deptService.isView = true;
        this.deptSubmitted = true;
        this.editDeptDetails = false;
        this.editMode = false;
        this.createMode = false;


        // var component = this;
        this.deptService.getTypes(this.dept.ID).subscribe(
          res => {
            // console.log("response at component is: "+ res);    
            if (res) {
              // console.log('array');
              this.deptService.selectedDepartment.DTypes = res;

            }
            else {
              // console.log(typeof(res));
              this.deptService.selectedDepartment.DTypes = [];

            }
            this.dept = this.deptService.selectedDepartment;
            this.typeGridOptions.rowData = this.dept.DTypes;
          }
        );
      }
    );
  }
  cancelEditDTypeDetails() {
    this.typeSubmitted = true;
    this.editTypeDetails = false;
    this.editTypeMode = false;
    this.showTypeAlertMsg = false;
    this.deptService.getType(this.dtype.ID).subscribe(
      res => {
        var obj;
        // console.log("response at component is: "+ res);    
        if (res) {
          // console.log(res[0]);
          obj = res[0];
          // this.dtype.ID = obj.ID;
          // this.dtype.TypeName = obj.TypeName;
          // this.dtype.TypeDesc = obj.TypeDesc;
          this.viewEditDType(obj);

        }
        else {
          // console.log(typeof(res));

        }
      }
    );

  }
  cancelEditSubTypeDetails() {
    this.subTypeSubmitted = true;
    this.editSubTypeDetails = false;
    this.editSubTypeMode = false;
    this.showSubTypeAlertMsg = false;
    this.deptService.getSubType(this.subType.ID).subscribe(
      res => {
        var obj;
        // console.log("response at component is: "+ res);    
        if (res) {
          // console.log(res[0]);
          obj = res[0];
          // this.viewEditSubType(obj);
          this.showSubTypeAlertMsg = false;
          this.showTypeAlertMsg = false;

          this.typeSection = false;
          this.subTypeSection = true;
          this.createSubTypeMode = true;
          this.subType = obj;

          this.subTypeSubmitted = true;
          this.editSubTypeDetails = false;
          this.editSubTypeMode = false;
          this.createSubTypeMode = false;

          this.showViewOnlySubTypeMode = false;
          this.updateSubTypeGridData();

        }
        else {
          // console.log(typeof(res));

        }
      }
    );

  }
  updateSubTypeGridData() {
    this.deptService.getSubTypes(this.dtype.ID).subscribe(
      res => {
        if (res) {
          // console.log('array');
          this.dtype.SubTypes = res;
          // this.router.navigate(['/actionDepartment']);
          this.subTypeGridOptions.rowData = this.dtype.SubTypes;
          this.subTypeGridApi.setRowData(res);
        }
        else {
          // console.log(typeof(res));
          this.dtype.SubTypes = [];
          // this.router.navigate(['/actionDepartment']);
          this.subTypeGridOptions.rowData = this.dtype.SubTypes;
          this.subTypeGridApi.setRowData(res);
        }


      }
    );
  }
  newDepartment() {
    this.dept = new Department();
    // this.submitted = false;
  }
  close() {
    this.router.navigate(['/departments']);
  }

  addNewType(dept) {
    this.showDeptAlertMsg = false;
    this.showTypeAlertMsg = false;
    this.departmentSection = false;
    this.typeSection = true;
    this.subTypeSection = false;
    // this.dtype.DepartmentID = this.dept.ID;
    this.createTypeMode = true;
    this.dtype = new DType(this.dept.ID);

    this.typeSubmitted = false;
    this.editTypeDetails = false;
    this.editTypeMode = false;
    this.createTypeMode = true;
  }
  addSubType(dtype) {
    // console.log(dtype);
    this.showSubTypeAlertMsg = false;
    this.showTypeAlertMsg = false;

    this.typeSection = false;
    this.subTypeSection = true;
    this.createSubTypeMode = true;
    this.subType = new SubType(this.dtype.ID);

    this.subTypeSubmitted = false;
    this.editSubTypeDetails = false;
    this.editSubTypeMode = false;
    this.createSubTypeMode = true;

    this.showViewOnlySubTypeMode = true;
  }

  // closeTypeDetails
  closeTypeCreate() {
    this.departmentSection = true;
    this.typeSection = false;
    this.subTypeSection = false;

    this.createTypeMode = false;

    this.showDeptAlertMsg = false;
    this.showTypeAlertMsg = false;

    this.dtype = new DType(this.dept.ID);

  }

  closeSubTypeCreate() {
    this.departmentSection = false;
    this.typeSection = true;
    this.subTypeSection = false;

    this.createSubTypeMode = false;

    this.showDeptAlertMsg = false;
    this.showTypeAlertMsg = false;
    this.showSubTypeAlertMsg = false;
    let obj = this.dtype;

    this.deptService.getSubTypes(obj.ID).subscribe(
      res => {
        if (res) {
          // console.log('array');
          this.dtype.SubTypes = res;
          // this.router.navigate(['/actionDepartment']);
          this.subTypeGridOptions.rowData = this.dtype.SubTypes;
          this.subTypeGridApi.setRowData(res);
        }
        else {
          // console.log(typeof(res));
          this.dtype.SubTypes = [];
          // this.router.navigate(['/actionDepartment']);
          this.subTypeGridOptions.rowData = this.dtype.SubTypes;
          this.subTypeGridApi.setRowData(res);
        }


      }
    );
  }

  viewEditDType(obj) {

    this.deptService.getSubTypes(obj.ID).subscribe(
      res => {
        // console.log("response at component is: "+ res);  
        this.showTypeAlertMsg = false;
        this.showTypeAlertMsg = false;

        this.departmentSection = false;
        this.typeSection = true;

        this.dtype = new DType(this.dept.ID);

        this.dtype.ID = obj.ID;
        this.dtype.TypeName = obj.TypeName;
        this.dtype.TypeDesc = obj.TypeDesc;

        if (res) {
          // console.log('array');
          this.dtype.SubTypes = res;
          // this.router.navigate(['/actionDepartment']);
          this.subTypeGridOptions.rowData = this.dtype.SubTypes;
        }
        else {
          // console.log(typeof(res));
          this.dtype.SubTypes = [];
          // this.router.navigate(['/actionDepartment']);
          this.subTypeGridOptions.rowData = this.dtype.SubTypes;
        }

        this.typeSubmitted = true;
        this.editTypeDetails = false;
        this.editTypeMode = false;
        this.createTypeMode = false;

      }
    );

  }

  deleteDType(obj) {
    // console.log(obj);
    $('#deleteTypeConfirmationModel').modal('show');

    this.selectedTypeID = obj.ID;
    this.selectedTypeName = obj.TypeName;

  }
  confirmDeleteType() {
    //   this.selectedID = 2;
    $('#deleteTypeConfirmationModel').modal('hide');
    this.deptService.deleteType(this.selectedTypeID).subscribe(
      res => {
        if (res) {

          this.deptService.getTypes(this.dept.ID).subscribe(
            res => {
              //  console.log(res);
              if (res) {
                this.typeGridOptions.rowData = res;
                this.typeGridApi.setRowData(res);
              }
              else {
                this.typeGridOptions.rowData = [];
                this.typeGridApi.setRowData([]);
              }



            });

        }
      });

  }
  cancelDeleteType() {
    $('#deleteTypeConfirmationModel').modal('hide');
  }
  viewEditSubType(obj) {

    this.showSubTypeAlertMsg = false;
    this.showTypeAlertMsg = false;

    this.typeSection = false;
    this.subTypeSection = true;
    this.createSubTypeMode = true;
    this.subType = obj;

    this.subTypeSubmitted = true;
    this.editSubTypeDetails = false;
    this.editSubTypeMode = false;
    this.createSubTypeMode = false;

    this.showViewOnlySubTypeMode = false;


  }
  deleteSubType(obj) {
    // console.log(obj);
    $('#deleteSubTypeConfirmationModel').modal('show');
    this.selectedSubTypeID = obj.ID;
    this.selectedSubTypeName = obj.SubTypeName;
  }
  confirmDeleteSubType() {
    //   this.selectedID = 2;
    $('#deleteSubTypeConfirmationModel').modal('hide');
    this.deptService.deleteSubType(this.selectedSubTypeID).subscribe(
      res => {
        if (res) {

          this.deptService.getSubTypes(this.dtype.ID).subscribe(
            res => {
              // console.log(res);
              if (res) {
                this.subTypeGridOptions.rowData = res;
                this.subTypeGridApi.setRowData(res);
              }
              else {
                this.subTypeGridOptions.rowData = [];
                this.subTypeGridApi.setRowData([]);
              }
              // this.storesGridOptions.rowData = res;

            });

        }
      });

  }
  cancelDeleteSubType() {
    $('#deleteConfirmationModel').modal('hide');
  }

  onTypeGridReady(params) {
    this.typeGridApi = params.api;
    this.typeGridColumnApi = params.columnApi;
  }

  onTypeGridSizeChanged(params) {
    var gridWidth = document.getElementById("typeGrid").offsetWidth;
    var columnsToShow = [];
    var columnsToHide = [];
    var totalColsWidth = 0;
    var allColumns = params.columnApi.getAllColumns();
    for (var i = 0; i < allColumns.length; i++) {
      let column = allColumns[i];
      totalColsWidth += column.getMinWidth();
      if (totalColsWidth > gridWidth) {
        columnsToHide.push(column.colId);
      } else {
        columnsToShow.push(column.colId);
      }
    }
    params.columnApi.setColumnsVisible(columnsToShow, true);
    params.columnApi.setColumnsVisible(columnsToHide, false);
    params.api.sizeColumnsToFit();
  }

  onSubTypeGridReady(params) {
    this.subTypeGridApi = params.api;
    this.subTypeGridColumnApi = params.columnApi;
  }

  onSubTypeGridSizeChanged(params) {
    var gridWidth = document.getElementById("subTypeGrid").offsetWidth;
    var columnsToShow = [];
    var columnsToHide = [];
    var totalColsWidth = 0;
    var allColumns = params.columnApi.getAllColumns();
    for (var i = 0; i < allColumns.length; i++) {
      let column = allColumns[i];
      totalColsWidth += column.getMinWidth();
      if (totalColsWidth > gridWidth) {
        columnsToHide.push(column.colId);
      } else {
        columnsToShow.push(column.colId);
      }
    }
    params.columnApi.setColumnsVisible(columnsToShow, true);
    params.columnApi.setColumnsVisible(columnsToHide, false);
    params.api.sizeColumnsToFit();
  }

}
