import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable ,  of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import {POTypes} from '../helpers/poTypes';

@Injectable({
  providedIn: 'root'
})
export class PoTypeService {

  private poTypeUrl;
  public resultData;
  public selectedPOType: POTypes; // used for editing POTypes
  public isEditPOType=false;
  public isCreatePOType=false;
  public isViewPOType=false;
  
  constructor(private httpClient: HttpClient) {
    this.poTypeUrl = 'assets/api/potypes.php';
    this.selectedPOType = new POTypes();
  }
  getAllPOTypes (): Observable<any> {
    var obj = {'action':'getAllPOTypes',potype:{id:0}};
    return this.httpClient.post(this.poTypeUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllPOTypes', []))
      );
  }
  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
      // TODO: better job of transforming error for user consumption
      //this.log(`${operation} failed: ${error.message}`);
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}
