import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";

@Component({
  selector: 'app-sub-type-edit-delete-btn',
  templateUrl: './sub-type-edit-delete-btn.component.html',
  styleUrls: ['./sub-type-edit-delete-btn.component.css']
})
export class SubTypeEditDeleteBtnComponent implements ICellRendererAngularComp {

  constructor() { }
  public params: any;

  agInit(params: any): void {
    this.params = params;
  }

  public viewEditSubType() {
    this.params.context.componentParent.viewEditSubType(this.params.node.data);
  }

  public deleteSubType() {
    this.params.context.componentParent.deleteSubType(this.params.node.data);
  }
  refresh(): boolean {
    return false;
  }

}
