import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";

@Component({
  selector: 'app-product-edit-delete-btn',
  templateUrl: './product-edit-delete-btn.component.html',
  styleUrls: ['./product-edit-delete-btn.component.css']
})
export class ProductEditDeleteBtnComponent implements ICellRendererAngularComp {

  constructor() { }
  public params: any;

  agInit(params: any): void {
    this.params = params;
  }

  public viewEditProduct() {
    this.params.context.componentParent.viewEditProduct(this.params.node.data);
  }

  public deleteProduct() {
    this.params.context.componentParent.deleteProduct(this.params.node.data);
  }
  refresh(): boolean {
    return false;
  }

}
