import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable ,  of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { SwRange } from '../helpers/sw-range';

@Injectable()
export class SizeWidthService {

  private swRangeUrl;
  public selectedSWRange: SwRange; // used for editing store
  public isEditSWRange=false;
  public isCreateSWRange=false;
  public isViewSWRange=false;
  
  constructor(private httpClient: HttpClient) { 
    this.swRangeUrl = 'assets/api/swRange.php';
  }

  getAllSWRange (): Observable<any> {
    var obj = {'action':'getAllSWRange',swRange:{id:1}};
    return this.httpClient.post(this.swRangeUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllSWRange', []))
      );
  }
  getSWRange (swRangeID): Observable<any> {
    var obj = {'action':'getSWRange',swRangeID:swRangeID};
    return this.httpClient.post(this.swRangeUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getSWRange', []))
      );
  }
  getAllSWRangeForBrand(brandID):Observable<any> {
    var obj = {'action':'getAllSWRangeForBrand',brandID:brandID};
    return this.httpClient.post(this.swRangeUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllSWRangeForBrand', []))
      );
  }
  getAllSWRangeFromProduct(productID):Observable<any> {
    var obj = {'action':'getAllSWRangeFromProduct',productID:productID};
    return this.httpClient.post(this.swRangeUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('getAllSWRangeFromProduct', []))
      );
  }
  addSWRange (swRange): Observable<any> {
    var obj = {'action':'addNewSWRange',swRange:swRange};
    return this.httpClient.post(this.swRangeUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('addSWRange', []))
      );
  }
  updateSWRange (swRange): Observable<any> {
    var obj = {'action':'updateSWRange',swRange:swRange};
    return this.httpClient.post(this.swRangeUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('updateSWRange', []))
      );
  }
  deleteSWRange (swRange): Observable<any> {
    var obj = {'action':'deleteSWRange',swRange:swRange};
    return this.httpClient.post(this.swRangeUrl,{
          headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded;charset=utf-8'),
          responseType: 'json',
          params:obj })
      .pipe(
        catchError(this.handleError('deleteSWRange', []))
      );
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
      // TODO: better job of transforming error for user consumption
      //this.log(`${operation} failed: ${error.message}`);
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  toExcelHeaderString = function (rows) {
    return this.toExcelHeaderArray(rows).join(",");
}

toExcelHeaderArray = function (rows) {
    var excelHeaderArr = [];
    for(var index = 1; index <= rows; index++) {
        excelHeaderArr.push(this.toExcelHeader(index));
    }
    return excelHeaderArr;
}

toExcelHeader = function (index) {
    if(index <= 0) {
        throw new Error("index must be 1 or greater");
    }
    // index--;
    var charCodeOfA = ("a").charCodeAt(0); // you could hard code to 97
    var charCodeOfZ = ("z").charCodeAt(0); // you could hard code to 122
    var excelStr = "";
    var base24Str = (index).toString(charCodeOfZ - charCodeOfA + 1);
    for(var base24StrIndex = 0; base24StrIndex < base24Str.length; base24StrIndex++) {
        var base24Char = base24Str[base24StrIndex];
        var alphabetIndex = (base24Char * 1 == base24Char) ? base24Char : (base24Char.charCodeAt(0) - charCodeOfA + 10);
        // bizarre thing, A==1 in first digit, A==0 in other digits
        if(base24StrIndex == 0) {
            alphabetIndex -= 1;
        }
        excelStr += String.fromCharCode(charCodeOfA*1 + alphabetIndex*1);
    }
    return excelStr.toUpperCase();
}

}
