import { Component } from '@angular/core';
import { DataserviceService } from './services/dataservice.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'ExportPO';

  EPORoutes = [
    { title: "Purchase Orders", routelink: 'purchaseOrders', active: false, index: 0 },
    // { title: "Brands", routelink: "brands", active: false, index: 1 },
    // { title: "Departments", routelink: "departments", active: false, index: 2 },
    // { title: "Stores", routelink: "stores", active: false, index: 3 },
    // { title: "Size/Width-Range", routelink: "sizeWidthRange", active: false, index: 4 }
  ];

  setActive(index) {
    let length = this.EPORoutes.length;
    for (let i = 0; i < length; i++) {
      if (index == this.EPORoutes[i].index) {
        this.EPORoutes[i].active = true;
      }
      else {
        this.EPORoutes[i].active = false;
      }
    }
  }
  loginbtn: boolean;
  logoutbtn: boolean;
  constructor(private dataService: DataserviceService, private router: Router, private route: ActivatedRoute,) {
    dataService.getLoggedInName.subscribe(name => this.changeName(name));
    if (this.dataService.isLoggedIn()) {
      console.log("loggedin");
      this.loginbtn = false;
      this.logoutbtn = true
    }
    else {
      this.loginbtn = true;
      this.logoutbtn = false
    }


  }
  private changeName(name: boolean): void {
    this.logoutbtn = name;
    this.loginbtn = !name;
  }
  logout() {
    this.dataService.deleteToken();
    // window.location.href = window.location.href;
    this.router.navigate(['/']);
  }
}
