import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";

@Component({
  selector: 'app-swrange-edit-delete-btn',
  templateUrl: './swrange-edit-delete-btn.component.html',
  styleUrls: ['./swrange-edit-delete-btn.component.css']
})
export class SwrangeEditDeleteBtnComponent implements ICellRendererAngularComp {

  constructor() { }

  public params: any;

  agInit(params: any): void {
    this.params = params;
  }

  public viewSWRange() {
    this.params.context.componentParent.viewSWRange(this.params.node.data);
  }

  public editSWRange() {
    this.params.context.componentParent.editSWRange(this.params.node.data);
  }

  public deleteSWRange() {
    this.params.context.componentParent.deleteSWRange(this.params.node.data);
  }
  refresh(): boolean {
    return false;
  }

}
