import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
declare var jquery: any;
declare var $: any;
import { SizeWidthService } from '../../services/size-width.service';
import { GridOptions } from "ag-grid";
import { SwRange } from '../../helpers/sw-range';
import { SwrangeEditDeleteBtnComponent } from '../../helper-components/swrange-edit-delete-btn/swrange-edit-delete-btn.component';


@Component({
  selector: 'app-size-width',
  templateUrl: './size-width.component.html',
  styleUrls: ['./size-width.component.css']
})
export class SizeWidthComponent implements OnInit {

  public selectedSWRange: SwRange;
  public swRangeGridApi;
  public swRangeGridColumnApi;
  private swRangeGridOptions: GridOptions;
  public columnDefs;
  public isActive: boolean;

  constructor(private router: Router, private swRangeService: SizeWidthService) {
    this.isActive = false;
    this.selectedSWRange = new SwRange();

    this.swRangeGridOptions = <GridOptions>{
      context: {
        componentParent: this
      },
      enableColResize: true,
      enableSorting: true,
      enableFilter: true,
      pagination: true,
      paginationAutoPageSize: true
    };
    this.columnDefs = [
      {
        headerName: "ID",
        field: "ID",
        minWidth: 100,
        maxWidth: 100
      },
      {
        headerName: "Size/Width Range Name",
        field: "SizeWidthName",
        minWidth: 200,

      },
      {
        headerName: "Range Type",
        field: "RangeType",
        minWidth: 200,
      },
      {
        headerName: "Action",
        cellRendererFramework: SwrangeEditDeleteBtnComponent,
        maxWidth: 250
      }

    ];



  } //end of constructor

  ngOnInit() {
    //this.swRangeSelected = new SwRange();
    // console.log(this.swRangeSelected.SizeWidth[0][0]);
    this.loadSWRanges();
  }
  loadSWRanges() {
    this.swRangeService.getAllSWRange().subscribe(
      res => {
        //console.log("response at component is: "+ res);    
        if (res) {
          //console.log('array returned');
          this.swRangeGridOptions.rowData = res;
        }
        else {
          //console.log(typeof(res));
          this.swRangeGridOptions.rowData = []
        }
        // this.swRangeGridOptions.rowData = res;
        this.isActive = true;
      }
    );
  }
  viewSWRange(obj) {
    // console.log(params.node.data);

    // this.swRangeService.selectedSWRange = params.node.data;
    // this.swRangeService.isEditSWRange = false;
    // this.swRangeService.isCreateSWRange = false;
    // this.swRangeService.isViewSWRange=true;
    this.swRangeService.selectedSWRange = obj;
    this.swRangeService.isEditSWRange = false;
    this.swRangeService.isCreateSWRange = false;
    this.swRangeService.isViewSWRange = true;

    this.router.navigate(['/actionSWRange', 'view', obj.ID]);
  }
  addNewSizeWidthRange() {
    // this.swRangeService.isEditSWRange = false;
    // this.swRangeService.isCreateSWRange = true;
    // this.swRangeService.isViewSWRange=false;

    this.router.navigate(['/actionSWRange', 'create', '0']);

  }
  editSWRange(obj) {
    // console.log(obj);
    // this.swRangeService.selectedSWRange = obj;
    // this.swRangeService.isEditSWRange = true;
    // this.swRangeService.isCreateSWRange = false;
    // this.swRangeService.isViewSWRange=false;
    this.router.navigate(['/actionSWRange', 'edit', obj.ID]);

  }

  deleteSWRange(obj) {
    //console.log("here-2");
    //console.log(obj);
    this.selectedSWRange = obj;
    $('#deleteConfirmationModel').modal('show');

  }

  confirmDeleteSWRange() {
    // this.selectedID = 2;
    $('#deleteConfirmationModel').modal('hide');
    this.swRangeService.deleteSWRange(this.selectedSWRange).subscribe(
      res => {
        if (res) {

          this.swRangeService.getAllSWRange().subscribe(
            res => {
              //console.log(res);
              if (res) {
                this.swRangeGridApi.setRowData(res);
              }
              else {
                this.swRangeGridApi.setRowData([]);
              }
              // this.storesGridOptions.rowData = res;

            });

        }
      });

  }

  cancelDeleteSWRange() {
    $('#deleteConfirmationModel').modal('hide');

  }

  onGridReady(params) {
    this.swRangeGridApi = params.api;
    this.swRangeGridColumnApi = params.columnApi;
  }

  onGridSizeChanged(params) {
    var gridWidth = document.getElementById("swRangeGrid").offsetWidth;
    var columnsToShow = [];
    var columnsToHide = [];
    var totalColsWidth = 0;
    var allColumns = params.columnApi.getAllColumns();
    for (var i = 0; i < allColumns.length; i++) {
      let column = allColumns[i];
      totalColsWidth += column.getMinWidth();
      if (totalColsWidth > gridWidth) {
        columnsToHide.push(column.colId);
      } else {
        columnsToShow.push(column.colId);
      }
    }
    params.columnApi.setColumnsVisible(columnsToShow, true);
    params.columnApi.setColumnsVisible(columnsToHide, false);
    params.api.sizeColumnsToFit();
  }
}
