import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
declare var jquery: any;
declare var $: any;
import { StoresService } from '../../services/stores.service';
import { GridOptions } from "ag-grid";
import { StoresEditDeleteBtnComponent } from '../../helper-components/stores-edit-delete-btn/stores-edit-delete-btn.component';
import { Store } from '../../helpers/store';
import { Observable } from 'rxjs';




@Component({
  selector: 'app-stores',
  templateUrl: './stores.component.html',
  styleUrls: ['./stores.component.css']
})
export class StoresComponent implements OnInit {

  public storesGridApi;
  public storesGridColumnApi;
  private storesGridOptions: GridOptions;
  public columnDefs;
  private selectedID;
  selectedStore: Store;
  public isActive: boolean;


  constructor(private router: Router, private storesService: StoresService) {
    this.isActive = false;
    this.selectedStore = new Store();
    this.selectedID = 1;
    // this.selectedStore.StoreName = '';
    this.storesGridOptions = <GridOptions>{
      context: {
        componentParent: this
      },
      enableColResize: true,
      enableSorting: true,
      enableFilter: true,
      pagination: true,
      paginationAutoPageSize: true
    };
    this.columnDefs = [
      {
        headerName: "ID",
        field: "ID",
        minWidth: 100,
        maxWidth: 100
      },
      {
        headerName: "Store Name",
        field: "StoreName",
        minWidth: 200,

      },
      {
        headerName: "Store Number",
        field: "StoreNumber",
        minWidth: 200,
      },
      {
        headerName: "Action",
        cellRendererFramework: StoresEditDeleteBtnComponent,
        maxWidth: 150
      }

    ];
  } // end of constructor
  ngOnInit() {
    this.loadStores();
  }

  loadStores() {
    this.storesService.getAllStores().subscribe(
      res => {
        //console.log("response at component is: "+ res);    
        if (res) {
          //console.log('array');
          this.storesGridOptions.rowData = res;
        }
        else {
          //console.log(typeof(res));
          this.storesGridOptions.rowData = [];
        }

        this.isActive = true;
      }
    );
  }


  addNewStore() {
    this.router.navigate(['/addStore'])
  }

  editStore(obj) {
    // console.log(obj);
    // this.storesService.selectedStore = obj;
    // console.log(this.storesService.selectedStore);
    this.router.navigate(['/editStore', obj.ID]);
  }

  deleteStore(obj) {
    //console.log(obj);
    $('#deleteConfirmationModel').modal('show');
    this.selectedStore = obj;
  }
  confirmDeleteStore() {
    this.selectedID = 2;
    $('#deleteConfirmationModel').modal('hide');
    this.storesService.deleteStore(this.selectedStore).subscribe(
      res => {
        if (res) {

          this.storesService.getAllStores().subscribe(
            res => {
              //console.log(res);
              if (res) {
                this.storesGridApi.setRowData(res);
              }
              else {
                this.storesGridApi.setRowData([]);
              }
              // this.storesGridOptions.rowData = res;

            });

        }
      });

  }

  cancelDeleteStore() {
    $('#deleteConfirmationModel').modal('hide');
  }

  onGridReady(params) {
    this.storesGridApi = params.api;
    this.storesGridColumnApi = params.columnApi;
  }

  onGridSizeChanged(params) {
    var gridWidth = document.getElementById("storesGrid").offsetWidth;
    var columnsToShow = [];
    var columnsToHide = [];
    var totalColsWidth = 0;
    var allColumns = params.columnApi.getAllColumns();
    for (var i = 0; i < allColumns.length; i++) {
      let column = allColumns[i];
      totalColsWidth += column.getMinWidth();
      if (totalColsWidth > gridWidth) {
        columnsToHide.push(column.colId);
      } else {
        columnsToShow.push(column.colId);
      }
    }
    params.columnApi.setColumnsVisible(columnsToShow, true);
    params.columnApi.setColumnsVisible(columnsToHide, false);
    params.api.sizeColumnsToFit();
  }

}
