import { Component, OnInit } from '@angular/core';
import { ICellRendererAngularComp } from "ag-grid-angular";
@Component({
  selector: 'app-brand-edit-delete-btn',
  templateUrl: './brand-edit-delete-btn.component.html',
  styleUrls: ['./brand-edit-delete-btn.component.css']
})
export class BrandEditDeleteBtnComponent implements ICellRendererAngularComp {

  constructor() { }

  public params: any;

  agInit(params: any): void {
    this.params = params;
  }

  public viewBrand() {
    this.params.context.componentParent.viewBrand(this.params.node.data);
  }

  public editBrand() {
    this.params.context.componentParent.editBrand(this.params.node.data);
  }

  public deleteBrand() {
    this.params.context.componentParent.deleteBrand(this.params.node.data);
  }
  refresh(): boolean {
    return false;
  }

}
