import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';

import { PurchaseOrderComponent } from './components/purchase-order/purchase-order.component';
import { VendorComponent } from './components/vendor/vendor.component';
import { BrandComponent } from './components/brand/brand.component';
import { DepartmentComponent } from './components/department/department.component';
import { StoresComponent } from './components/stores/stores.component';
import { SizeWidthComponent } from './components/size-width/size-width.component';
import { CreateStoreComponent } from './components/create-store/create-store.component';
import { EditStoreComponent } from './components/edit-store/edit-store.component';
import { CreateSwRangeComponent } from './components/create-sw-range/create-sw-range.component';
import { ActionDepartmentComponent } from './components/action-department/action-department.component';
import { ActionBrandComponent } from './components/action-brand/action-brand.component';
import { ActionPurchaseOrderComponent } from './components/action-purchase-order/action-purchase-order.component';
import { AuthguardGuard } from './guards/authguard.guard';
import { LoginComponent } from './components/login/login.component';
import { ExportImportActionsComponent } from './components/export-import-actions/export-import-actions.component';

const routes: Routes = [
  {
    path: '',
    component: LoginComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'purchaseOrders',
    component: PurchaseOrderComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'brands',
    component: BrandComponent,
    pathMatch: 'full',
    canActivate: [AuthguardGuard]
  },
  {
    path: 'departments',
    component: DepartmentComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'stores',
    component: StoresComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'sizeWidthRange',
    component: SizeWidthComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'addStore',
    component: CreateStoreComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'editStore/:id',
    component: EditStoreComponent,
    canActivate: [AuthguardGuard]
  },

  {
    path: 'actionSWRange/:action/:id',
    component: CreateSwRangeComponent,
    canActivate: [AuthguardGuard]

  },

  {
    path: 'actionBrand/:action/:id',
    component: ActionBrandComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'actionBrand/:action',
    component: ActionBrandComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'actionBrand',
    component: ActionBrandComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'actionPO/:id',
    component: ActionPurchaseOrderComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'actionPO',
    component: ActionPurchaseOrderComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'exportImportActions/:id',
    component: ExportImportActionsComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: 'actionDepartment/:action/:id',
    component: ActionDepartmentComponent,
    canActivate: [AuthguardGuard]
  },
  {
    path: '',
    redirectTo: '/login',
    pathMatch: 'full'
  },
];

@NgModule({
  exports: [RouterModule],
  imports: [
    CommonModule,
    RouterModule.forRoot(routes, { useHash: true })
  ],
  declarations: []
})
export class AppRoutingModule { }
