import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GridOptions } from "ag-grid";
import { Observable } from 'rxjs';
import { PurchaseOrder } from '../../helpers/purchase-order';
import { PurchaseOrderService } from '../../services/purchase-order.service';
import { PurchaseOrderEditDeleteBtnComponent } from '../../helper-components/purchase-order-edit-delete-btn/purchase-order-edit-delete-btn.component';
import * as numeral from 'numeral';
declare var jquery: any;
declare var $: any;

@Component({
  selector: 'app-purchase-order',
  templateUrl: './purchase-order.component.html',
  styleUrls: ['./purchase-order.component.css']
})
export class PurchaseOrderComponent implements OnInit {
  public poGridApi;
  public poGridColumnApi;
  private poGridOptions: GridOptions;
  public columnDefs;
  private selectedID;
  selectedPO: PurchaseOrder;

  public isActive: boolean;

  constructor(private router: Router, private poService: PurchaseOrderService) {
    this.isActive = false;
    this.selectedPO = new PurchaseOrder();
    this.selectedID = 1;
    // this.selectedStore.StoreName = '';
    this.poGridOptions = <GridOptions>{
      context: {
        componentParent: this
      },
      enableColResize: true,
      enableSorting: true,
      enableFilter: true,
      pagination: true,
      paginationAutoPageSize: true,
      suppressHorizontalScroll: false,
      // suppressAnimationFrame:true,
      // scrollbarWidth:20
    };
    this.columnDefs = [
      {
        headerName: "ID",
        field: "ID",
        filter: 'agNumberColumnFilter',
        minWidth: 50,
        maxWidth: 50,
      },
      {
        headerName: "PO Type",
        field: "POType",
        minWidth: 70,
      },
      {
        headerName: "Order Date",
        field: "OrderDate",
        minWidth: 100,
      },
      {
        headerName: "Brand",
        field: "BrandName",
        minWidth: 100,
      },
      {
        headerName: "Purchase Order #",
        field: "PurchaseOrderNumber",
        minWidth: 150,

      },
      {
        headerName: "Ship Date",
        field: "ShipDate",
        minWidth: 100,
      },
      {
        headerName: "Total Quantity",
        field: "TotalQuantity",
        minWidth: 80,
      },
      {
        headerName: "Total $ Ordered",
        field: "TotalCost",
        valueFormatter: currencyFormatter,
        minWidth: 100,
        // pinned:'right'
      },
      {
        headerName: "Action",
        cellRendererFramework: PurchaseOrderEditDeleteBtnComponent,
        maxWidth: 300,
        minWidth: 300
      }

    ];

  }
  ngOnInit() {
    this.loadPurchaseOrders();
  }

  loadPurchaseOrders() {
    this.poService.getAllPurchaseOrders().subscribe(
      res => {
        //console.log("response at component is: "+ res);    
        if (res) {
          //console.log('array');
          this.poGridOptions.rowData = res;
        }
        else {
          //console.log(typeof(res));
          this.poGridOptions.rowData = [];
        }
        this.isActive = true;
        try {
          this.poGridApi.setRowData(this.poGridOptions.rowData);
        }
        catch (e) {
        }

      }
    );
  }
  addNewPO() {
    this.poService.isEdit = false;
    this.poService.isCreate = true;
    this.poService.isView = false;
    this.router.navigate(['/actionPO']);

  }

  viewEditPO(obj) {
    //console.log(obj);
    this.poService.isEdit = false;
    this.poService.isCreate = false;
    this.poService.isView = true;
    this.poService.selectedPO.ID = obj.ID;
    this.poService.selectedPO.POType = obj.POType;
    this.poService.selectedPO.PurchaseOrderNumber = obj.PurchaseOrderNumber;
    this.poService.selectedPO.VendorID = obj.VendorID;
    this.poService.selectedPO.VendorName = obj.VendorName;
    this.poService.selectedPO.BrandID = obj.BrandID;
    this.poService.selectedPO.BrandName = obj.BrandName;
    this.poService.selectedPO.StoreID = obj.StoreID;
    this.poService.selectedPO.StoreName = obj.StoreName;
    this.poService.selectedPO.StoreNumber = obj.StoreNumber;

    // var x = "12/31/2017";
    var dv = obj.OrderDate.match(/^(\d{1,2})\/(\d{1,2})\/(\d{4})$/);
    this.poService.selectedPO.OrderDate = dv[3] + "-" + dv[1] + "-" + dv[2];

    // this.poService.selectedPO.OrderDate = obj.OrderDate;
    dv = obj.ShipDate.match(/^(\d{1,2})\/(\d{1,2})\/(\d{4})$/);
    this.poService.selectedPO.ShipDate = dv[3] + "-" + dv[1] + "-" + dv[2];

    dv = obj.CancelDate.match(/^(\d{1,2})\/(\d{1,2})\/(\d{4})$/);
    this.poService.selectedPO.CancelDate = dv[3] + "-" + dv[1] + "-" + dv[2];

    this.poService.selectedPO.Notes = obj.Notes;
    // var component = this;
    this.poService.getProducts(obj.ID).subscribe(
      res => {
        //console.log("response at component is: "+ res);    
        if (res) {
          //console.log('array');
          this.poService.selectedPO.products = res;
          this.router.navigate(['/actionPO/' + obj.ID]);
        }
        else {
          //console.log(typeof(res));
          this.poService.selectedPO.products = [];
          this.router.navigate(['/actionPO/' + obj.ID]);
        }

        this.isActive = true;
      }
    );
    // this.poService.selectedDepartment = obj;

    //console.log(this.poService.selectedPO);

  }

  deletePO(obj) {
    //console.log(obj);
    $('#deleteConfirmationModel').modal('show');
    this.selectedPO = obj;
  }

  exportPO(obj) {
    // window.open("assets/api/exportPO.php/?poID=" + obj.ID);

    this.router.navigate(['/exportImportActions/' + obj.ID]);
  }
  confirmDeletePO() {
    this.selectedID = 2;
    $('#deleteConfirmationModel').modal('hide');
    this.poService.deletePurchaseOrder(this.selectedPO).subscribe(
      res => {
        if (res) {

          this.poService.getAllPurchaseOrders().subscribe(
            res => {
              //console.log(res);
              if (res) {
                this.poGridApi.setRowData(res);
              }
              else {
                this.poGridApi.setRowData([]);
              }
              // this.storesGridOptions.rowData = res;

            });

        }
      });
  }

  cancelDeletePO() {
    $('#deleteConfirmationModel').modal('hide');
  }

  onGridReady(params) {
    this.poGridApi = params.api;
    this.poGridColumnApi = params.columnApi;
  }

  onGridSizeChanged(params) {
    var gridWidth = document.getElementById("poGrid").offsetWidth;
    var columnsToShow = [];
    var columnsToHide = [];
    var totalColsWidth = 0;
    var allColumns = params.columnApi.getAllColumns();
    for (var i = 0; i < allColumns.length; i++) {
      let column = allColumns[i];
      totalColsWidth += column.getMinWidth();
      //if (totalColsWidth > gridWidth) 
      {
        //columnsToHide.push(column.colId);
        //} else {
        columnsToShow.push(column.colId);
      }
    }
    params.columnApi.setColumnsVisible(columnsToShow, true);
    params.columnApi.setColumnsVisible(columnsToHide, false);
    params.api.sizeColumnsToFit();
  }
}


function currencyFormatter(params) {
  return formatNumber(params.value);
}
function formatNumber(number) {
  return numeral(number).format('0,0.00');
}